export default {
    name: 'rhombus',
    props: {
        value: {
            type: Number,
            default: () => new Number()
        }
    },
    computed: {
        boxShadow: function () {
            return `0 0 ${(1-this.value)*16}px white`;
        },
        dimension: function () {
            const unit = 'px';
            const outer = 80;
            const hypotenuse = outer;
            const cathetus = Math.sqrt(Math.pow(hypotenuse, 2) / 2);
            return {
                outer: hypotenuse + unit,
                inner: cathetus + unit,
                innerScaled: this.value * cathetus + unit,
            }
        }
    }
}

